/* eslint-disable react/no-danger */
// @flow

import React from 'react'
import {Link, graphql} from 'gatsby'
import styled from 'styled-components'

import PostHeader from './PostHeader'

import Layout from '../Layout'
import SEO from '../SEO'

type Props = {
  data: {
    markdownRemark: {|
      id: string,
      frontmatter: {|
        title: string,
        date: string,
        description: string,
        featuredImage: string,
      |},
      excerpt: any,
      html: string,
    |},
    site: {
      siteMetadata: {|
        title: string,
        author: {|
          name: string,
        |},
        social: {|
          twitter: string,
          facebook: string,
          github: string,
        |},
      |},
    },
  },
  pageContext: {
    previous: {
      fields: {
        slug: string,
      },
      frontmatter: {
        title: string,
      },
    },
    next: {
      fields: {
        slug: string,
      },
      frontmatter: {
        title: string,
      },
    },
  },
  location: Location,
}

// const FONT_NANUM_GOTHIC: string = 'Nanum Gothic'
// const FONT_NANUM_GOTHIC_CODING: string = 'Nanum Gothic Coding'

const MyArticle = styled.article`
  max-width: ${props => props.theme.spacing(84)}px;
  margin-left: auto;
  margin-right: auto;
  font-family: Georgia, Cambria, 'Times New Roman', Times, serif;
  font-size: 15px;
`

const HR = styled.hr``
// /* margin-bottom: ${rhythm(1)}; */

const UL = styled.ul`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  list-style: none;
  padding: 0px;
`

const MySection = styled.section`
  img {
    max-width: 100%;
  }
`

const Img = styled.img`
  max-width: 100%;
`

const Post = (props: Props) => {
  const {data, pageContext, location} = props

  const {
    markdownRemark: {
      frontmatter: {title, date, featuredImage},
      excerpt,
      html,
    },
    site: {
      siteMetadata: {
        title: siteTitle,
        author: {name},
      },
    },
  } = data

  const {previous, next} = pageContext

  return (
    <Layout location={location} title={siteTitle} posts={[]}>
      <SEO title={title} description={excerpt} />
      <MyArticle>
        <PostHeader title={title} date={date} author={name} />
        <Img src={featuredImage} alt={featuredImage} />
        <MySection dangerouslySetInnerHTML={{__html: html}} />
        <HR />
        <footer />
      </MyArticle>

      <nav>
        <UL>
          <li>
            {previous && (
              <Link to={previous.fields.slug} rel="prev">
                 {previous.frontmatter.title}
              </Link>
            )}
          </li>
          <li>
            {next && (
              <Link to={next.fields.slug} rel="next">
                {next.frontmatter.title} 
              </Link>
            )}
          </li>
        </UL>
      </nav>
    </Layout>
  )
}

export default Post

export const pageQuery = graphql`
  query($slug: String!) {
    site {
      siteMetadata {
        title
        author {
          name
        }
        social {
          twitter
          facebook
          github
        }
      }
    }
    markdownRemark(fields: {slug: {eq: $slug}}) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        date(formatString: "YYYY-MM-DD A hh:mm:ss")
        featuredImage
      }
    }
  }
`
